import axios from 'axios';
const API_URL = '/posts/'

const state = {
    posts: null
};

const getters = {
    posts: state => state.posts
};

const actions = {
    // Create posts
    async createPost({ commit }, newPost) {
        commit('createPostRequest');
        try {
            const res = await axios.post(`${API_URL}`, newPost);
            if (res.data.success) {
                commit('createPostSuccess');
            };
            return res;
        } catch (error) {
            console.log(error);
        };
    },

    // Get posts
    async getPosts({ commit }) {
        commit('getPostsRequest');
        try {
            const res = await axios.get(`${API_URL}`);
            if (res.data.success) {
                commit('getPostsSuccess', res.data.value);
            };
            return res;
        } catch (error) {
            console.log(error)
        }
    }
};

const mutations = {
    createPostRequest(state) {
        state.status = 'loading';
    },
    createPostSuccess(state) {
        state.status = 'success';
    },
    getPostsRequest(state) {
        state.status = 'loading';
    },
    getPostsSuccess(state, posts) {
        state.status = 'success';
        state.posts = posts;
    }
};

export default {
    state,
    actions,
    getters,
    mutations
};