<template>
  <v-card height="100%" width="100%" class="overflow-hidden">
    <v-navigation-drawer color right permanent absolute width="100%">
      <v-card class="mx-auto" max-width="300" tile>
        <v-list rounded v-if="people">
          <v-subheader>Who to Follow</v-subheader>
          <v-list-item-group color="primary">
            <v-list-item
              v-for="(person, i) in people"
              :key="i"
              @click="$router.push(person.username)"
            >
              <v-list-item-avatar>
                <v-img :src="person.decodedProfileImage"></v-img>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title v-text="person.username"></v-list-item-title>
              </v-list-item-content>
              <v-list-item-content>
                <v-btn
                  small
                  text
                  color="blue"
                  @click.stop="onFollow(person._id)"
                >{{loggedUser.follows.includes(person._id) ? 'Following' : 'Follow'}}</v-btn>
              </v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-card>
    </v-navigation-drawer>
  </v-card>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Rightbar",
  methods: {
    ...mapActions([
      "getPeople",
      "followPerson",
      "getProfile",
      "getUserProfile",
    ]),
    onFollow(personId) {
      this.followPerson(personId).then((res) => {
        this.getUserProfile();
        this.getProfile(this.$router.currentRoute.path.replace("/", ""));
      });
    },
  },
  computed: {
    ...mapGetters(["people", "loggedUser"]),
  },
  created() {
    this.getPeople();
  },
};
</script>

<style lang="scss" scoped>
aside > div > div {
  width: 100% !important;
}
</style>